// Entry point for the EdgeFunction worker threads.

import EdgeFunctionParentInterface from './EdgeFunctionParentInterface'
import { parentPort } from './Worker'

try {
  const parentInterface = new EdgeFunctionParentInterface()
  parentInterface.startListening()
} catch (e) {
  parentPort.postMessage(['error', e])
}
